	module.exports = {"data":{"postsMarkdown":{"id":"md-cjfk94jcmvrsd01767g4sqsym","title":"You don't like your job, you don't strike","date":"03/04/2018","tags":["simpsons","placeholder"],"category":"placeholder","html":"# Aaah! Natural light! Get it off me! Get it off me!\n\nI like my beer cold, my TV loud and my homosexuals flaming. And now, in the spirit of the season: start shopping. And for every dollar of Krusty merchandise you buy, I will be nice to a sick kid. For legal purposes, sick kids may include hookers with a cold.\n\nHow could you?! Haven't you learned anything from that guy who gives those sermons at church? Captain Whatshisname? We live in a society of laws! Why do you think I took you to all those Police Academy movies? For fun? Well, I didn't hear anybody laughing, did you? Except at that guy who made sound effects. Makes sound effects and laughs. Where was I? __Oh yeah!__ *Stay out of my booze.* Son, when you participate in sporting events, it's not whether you win or lose: it's how drunk you get.\n\n## Ahoy hoy?\n\n…And the fluffy kitten played with that ball of string all through the night. On a lighter note, a Kwik-E-Mart clerk was brutally murdered last night. Look out, Itchy! He's Irish! Whoa, slow down there, maestro. There's a *New* Mexico?\n\n1. When will I learn? The answers to life's problems aren't at the bottom of a bottle, they're on TV!\n2. Uh, no, you got the wrong number. This is 9-1…2.\n3. When I held that gun in my hand, I felt a surge of power…like God must feel when he's holding a gun.\n\n### A lifetime of working with nuclear power has left me with a healthy green glow…and left me as impotent as a Nevada boxing commissioner.\n\nMarge, it takes two to lie. One to lie and one to listen. Slow down, Bart! My legs don't know how to be as long as yours. Don't kid yourself, Jimmy. If a cow ever got the chance, he'd eat you and everyone you care about!\n\n* A lifetime of working with nuclear power has left me with a healthy green glow…and left me as impotent as a Nevada boxing commissioner.\n* Kids, kids. I'm not going to die. That only happens to bad people.\n* Mrs. Krabappel and Principal Skinner were in the closet making babies and I saw one of the babies and then the baby looked at me.\n\nSon, a woman is like a beer. They smell good, they look good, you'd step over your own mother just to get one! But you can't stop at one. You wanna drink another woman! I'll keep it short and sweet — Family. Religion. Friendship. These are the three demons you must slay if you wish to succeed in business.\n\nHi. I'm Troy McClure. You may remember me from such self-help tapes as \"Smoke Yourself Thin\" and \"Get Some Confidence, Stupid!\" Don't kid yourself, Jimmy. If a cow ever got the chance, he'd eat you and everyone you care about!\n\nWhat good is money if it can't inspire terror in your fellow man? No children have ever meddled with the Republican Party and lived to tell about it. Bart, with $10,000 we'd be millionaires! We could buy all kinds of useful things like…love!\n\nAttempted murder? Now honestly, what is that? Do they give a Nobel Prize for attempted chemistry? But, Aquaman, you cannot marry a woman without gills. You're from two different worlds… Oh, I've wasted my life.\n\nHow is education supposed to make me feel smarter? Besides, every time I learn something new, it pushes some old stuff out of my brain. Remember when I took that home winemaking course, and I forgot how to drive? Attempted murder? Now honestly, what is that? Do they give a Nobel Prize for attempted chemistry?\n\nThe Internet King? I wonder if he could provide faster nudity… Old people don't need companionship. They need to be isolated and studied so it can be determined what nutrients they have that might be extracted for our personal use.\n\nWhoa, slow down there, maestro. There's a *New* Mexico? \"Thank the Lord\"? That sounded like a prayer. A prayer in a public school. God has no place within these walls, just like facts don't have a place within an organized religion.\n\nI hope I didn't brain my damage. Can't you people take the law into your own hands? I mean, we can't be policing the entire city! Oh, loneliness and cheeseburgers are a dangerous mix. And now, in the spirit of the season: start shopping. And for every dollar of Krusty merchandise you buy, I will be nice to a sick kid. For legal purposes, sick kids may include hookers with a cold.\n\nAhoy hoy? Kids, you tried your best and you failed miserably. The lesson is, never try. Kids, we need to talk for a moment about Krusty Brand Chew Goo Gum Like Substance. We all knew it contained spider eggs, but the hantavirus? That came out of left field. So if you're experiencing numbness and/or comas, send five dollars to antidote, PO box…\n\nLisa, vampires are make-believe, like elves, gremlins, and Eskimos. Ahoy hoy? What good is money if it can't inspire terror in your fellow man? Dad didn't leave… When he comes back from the store, he's going to wave those pop-tarts right in your face!\n\nThe Internet King? I wonder if he could provide faster nudity… \"Thank the Lord\"? That sounded like a prayer. A prayer in a public school. God has no place within these walls, just like facts don't have a place within an organized religion.\n","childMarkdownRemark":{"html":"<h1 id=\"aaah-natural-light-get-it-off-me-get-it-off-me\"><a href=\"#aaah-natural-light-get-it-off-me-get-it-off-me\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Aaah! Natural light! Get it off me! Get it off me!</h1>\n<p>I like my beer cold, my TV loud and my homosexuals flaming. And now, in the spirit of the season: start shopping. And for every dollar of Krusty merchandise you buy, I will be nice to a sick kid. For legal purposes, sick kids may include hookers with a cold.</p>\n<p>How could you?! Haven't you learned anything from that guy who gives those sermons at church? Captain Whatshisname? We live in a society of laws! Why do you think I took you to all those Police Academy movies? For fun? Well, I didn't hear anybody laughing, did you? Except at that guy who made sound effects. Makes sound effects and laughs. Where was I? <strong>Oh yeah!</strong> <em>Stay out of my booze.</em> Son, when you participate in sporting events, it's not whether you win or lose: it's how drunk you get.</p>\n<h2 id=\"ahoy-hoy\"><a href=\"#ahoy-hoy\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Ahoy hoy?</h2>\n<p>…And the fluffy kitten played with that ball of string all through the night. On a lighter note, a Kwik-E-Mart clerk was brutally murdered last night. Look out, Itchy! He's Irish! Whoa, slow down there, maestro. There's a <em>New</em> Mexico?</p>\n<ol>\n<li>When will I learn? The answers to life's problems aren't at the bottom of a bottle, they're on TV!</li>\n<li>Uh, no, you got the wrong number. This is 9-1…2.</li>\n<li>When I held that gun in my hand, I felt a surge of power…like God must feel when he's holding a gun.</li>\n</ol>\n<h3 id=\"a-lifetime-of-working-with-nuclear-power-has-left-me-with-a-healthy-green-glowand-left-me-as-impotent-as-a-nevada-boxing-commissioner\"><a href=\"#a-lifetime-of-working-with-nuclear-power-has-left-me-with-a-healthy-green-glowand-left-me-as-impotent-as-a-nevada-boxing-commissioner\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>A lifetime of working with nuclear power has left me with a healthy green glow…and left me as impotent as a Nevada boxing commissioner.</h3>\n<p>Marge, it takes two to lie. One to lie and one to listen. Slow down, Bart! My legs don't know how to be as long as yours. Don't kid yourself, Jimmy. If a cow ever got the chance, he'd eat you and everyone you care about!</p>\n<ul>\n<li>A lifetime of working with nuclear power has left me with a healthy green glow…and left me as impotent as a Nevada boxing commissioner.</li>\n<li>Kids, kids. I'm not going to die. That only happens to bad people.</li>\n<li>Mrs. Krabappel and Principal Skinner were in the closet making babies and I saw one of the babies and then the baby looked at me.</li>\n</ul>\n<p>Son, a woman is like a beer. They smell good, they look good, you'd step over your own mother just to get one! But you can't stop at one. You wanna drink another woman! I'll keep it short and sweet — Family. Religion. Friendship. These are the three demons you must slay if you wish to succeed in business.</p>\n<p>Hi. I'm Troy McClure. You may remember me from such self-help tapes as \"Smoke Yourself Thin\" and \"Get Some Confidence, Stupid!\" Don't kid yourself, Jimmy. If a cow ever got the chance, he'd eat you and everyone you care about!</p>\n<p>What good is money if it can't inspire terror in your fellow man? No children have ever meddled with the Republican Party and lived to tell about it. Bart, with $10,000 we'd be millionaires! We could buy all kinds of useful things like…love!</p>\n<p>Attempted murder? Now honestly, what is that? Do they give a Nobel Prize for attempted chemistry? But, Aquaman, you cannot marry a woman without gills. You're from two different worlds… Oh, I've wasted my life.</p>\n<p>How is education supposed to make me feel smarter? Besides, every time I learn something new, it pushes some old stuff out of my brain. Remember when I took that home winemaking course, and I forgot how to drive? Attempted murder? Now honestly, what is that? Do they give a Nobel Prize for attempted chemistry?</p>\n<p>The Internet King? I wonder if he could provide faster nudity… Old people don't need companionship. They need to be isolated and studied so it can be determined what nutrients they have that might be extracted for our personal use.</p>\n<p>Whoa, slow down there, maestro. There's a <em>New</em> Mexico? \"Thank the Lord\"? That sounded like a prayer. A prayer in a public school. God has no place within these walls, just like facts don't have a place within an organized religion.</p>\n<p>I hope I didn't brain my damage. Can't you people take the law into your own hands? I mean, we can't be policing the entire city! Oh, loneliness and cheeseburgers are a dangerous mix. And now, in the spirit of the season: start shopping. And for every dollar of Krusty merchandise you buy, I will be nice to a sick kid. For legal purposes, sick kids may include hookers with a cold.</p>\n<p>Ahoy hoy? Kids, you tried your best and you failed miserably. The lesson is, never try. Kids, we need to talk for a moment about Krusty Brand Chew Goo Gum Like Substance. We all knew it contained spider eggs, but the hantavirus? That came out of left field. So if you're experiencing numbness and/or comas, send five dollars to antidote, PO box…</p>\n<p>Lisa, vampires are make-believe, like elves, gremlins, and Eskimos. Ahoy hoy? What good is money if it can't inspire terror in your fellow man? Dad didn't leave… When he comes back from the store, he's going to wave those pop-tarts right in your face!</p>\n<p>The Internet King? I wonder if he could provide faster nudity… \"Thank the Lord\"? That sounded like a prayer. A prayer in a public school. God has no place within these walls, just like facts don't have a place within an organized religion.</p>"},"author":{"name":"Dom Joe","id":"cjfk8wt9lvhwz0180g76kxzz0","avatar":{"id":"cjfk8w02zvr4s0116bd16rcle","fileName":"Don_Ramon","handle":"i44ILSTSROvtyYYI08Mg","width":422,"height":640},"biography":"Livin' la vida loca.","gitHubProfile":"GITHUB_USERNAME","linkedInProfile":"LINKEDIN_USERNAME","facebookProfile":"FACEBOOK_USERNAME","instagramProfile":"INSTAGRAM_USERNAME","twitterProfile":"TWITTER_USERNAME"},"coverImage":{"id":"cjfk943p1vjas0142ca6uzj22","handle":"2kjwSiFOT0q8EoYdYydb","fileName":"Bart_Simpson__You_squeeze_me_to_death_","width":640,"height":265},"slug":"you-dont-like-your-job-you-don't-strike"}},"pathContext":{"slug":"you-dont-like-your-job-you-don't-strike"}}

